.mobile-main-menu {

    width: 100%;
    padding: 20px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    .item {
        width: 49%;
        max-width: 250px;
        cursor: pointer;
        margin: 3px 0.5%;
        position: relative;
        img {
            max-width: 100%;
            border-radius: 5px;
        }
        .label {
            margin: 0;
            position: absolute;
            width: 70%;
            text-transform: capitalize;
            color: #fff;
            text-shadow: 1px 1px 2px rgb(102, 98, 98);
            font-weight: 600;
            font-size: calc(12px + 0.2vw);
            top: 3%;
            left: 3%;
        }
    }
}